import { DEFAULT_POSITION } from './constants';

export function isValidPosition(position: any) {
  return typeof position === 'number' && position >= 0 && position <= 1;
}

export function getSafePosition(position: any): number {
  return isValidPosition(position) ? position : DEFAULT_POSITION;
}

export default function loadImage(src: string) {
  return new Promise<void>((resolve, reject) => {
    const img = new Image();
    img.onload = () => {
      resolve();
    };
    img.onerror = () => {
      reject();
    };
    img.src = src;
  });
}
